.wcf--brand-slider-wrapper .swiper-wrapper {
  align-items: center;
  transition-timing-function: linear;
}
.wcf--brand-slider-wrapper .text-slide-content {
  display: flex;
  align-items: center;
  gap: var(--space-between);
}
.wcf--brand-slider-wrapper.slide-width-auto .swiper-slide {
  width: auto !important;
}